@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap);
.App{font-family:"Rubik",sans-serif;font-size:400;height:100vh;text-align:center;color:#2b2b2b}.App h1{color:white}.App h4,.App h5,.App h6{color:#969696;text-transform:uppercase}.App .header{position:relative}.App .header:before{content:"";position:absolute;top:0;left:0;background-image:url(/ip-address-tracker/static/media/pattern-bg.a60a3c85.png);width:100%;height:100%;background-repeat:no-repeat}.App .title{z-index:99;color:white;text-transform:uppercase}.App .radius{border-radius:0.75rem}.App .ip-list-group{display:flex;flex-direction:row;padding-left:0;margin-bottom:0;z-index:99;background-color:white;-webkit-transform:translateY(15%);transform:translateY(15%)}@media (min-width: 768px){.App .ip-list-group{width:95%}}@media (min-width: 992px){.App .ip-list-group{width:75%}}.App .ip-list-group .ip-list-group-item{position:relative;display:block;padding:0.5rem 1rem;color:#212529;text-align:start;text-decoration:none;background-color:#fff;border-right:0}@media (min-width: 575px){.App .ip-list-group .ip-list-group-item{border-right:1px solid rgba(0,0,0,0.125)}}@media (min-width: 575px){.App .ip-list-group .ip-list-group-item:last-child{border-right:0}}.App .ip-map-container{-webkit-transform:translateY(-30px);transform:translateY(-30px)}

